.site-header {
  padding-top: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (max-width: 600px) {
    align-items: start;
  }

  &__logo {
    width: 455px;
    margin: 0;

    @media (max-width: 900px) {
      width: 300px;
    }

    @media (max-width: 600px) {
      display: none;
    }
  }

  &__title {
    display: none;

    span {
      @media (max-width: 600px) {
        display: block;
        font-size: 10px;
        font-weight: 400;
        text-transform: uppercase;
        margin-top: 10px;
      }
    }

    @media (max-width: 600px) {
      display: block;
      font-size: 13px;
      font-weight: 600;
      text-transform: uppercase;
    }
  }
}