*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'Inter', sans-serif;
  color: #fff;
  background: linear-gradient(to right, rgba(3, 0, 24, 1), rgba(3, 0, 24, 1));
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: var(--primary-color, #fff);
}

td {
  padding: 0;
}

.section-title {
  margin-top: 0;
  margin-bottom: 45px;
  font-size: 40px;
  font-weight: 800;
  text-align: center;
}