.btn {
  display: inline-block;
  border-radius: 300px;
  background-color: #0e0e0e6b;
  border: 1px solid var(--secondary-color);
  color: #fff;
  padding: 25px 50px;
  padding-left: 100px;
  text-decoration: none;
  position: relative;
  font-size: 20px;
  line-height: 20px;

  &::before {
    content: '';
    height: 100%;
    width: 72px;
    border-radius: 1000px;
    position: absolute;
    left: 0;
    top: 0;
    background: url("data:image/svg+xml,%3Csvg width='30' height='30' viewBox='0 0 30 30' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15 0C6.72 0 0 6.72 0 15C0 23.28 6.72 30 15 30C23.28 30 30 23.28 30 15C30 6.72 23.28 0 15 0ZM18.99 17.595L17.07 18.705L15.15 19.815C12.675 21.24 10.65 20.07 10.65 17.22V15V12.78C10.65 9.915 12.675 8.76 15.15 10.185L17.07 11.295L18.99 12.405C21.465 13.83 21.465 16.17 18.99 17.595Z' fill='white'/%3E%3C/svg%3E%0A") var(--secondary-color);
    background-repeat: no-repeat;
    background-position: center;
  }
}