.text-with-icon-underline {
  position: relative;

  &::before {
    content: '';
    height: 9px;
    width: 100%;
    background: url("data:image/svg+xml,%3Csvg width='180' height='11' viewBox='0 0 180 11' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2 9C29.3581 4.83389 102.86 -1.83474 178 4.81967' stroke='%230587FF' stroke-width='3' stroke-linecap='round'/%3E%3C/svg%3E%0A") no-repeat;
    position: absolute;
    bottom: -10px;
    right: 0;
  }
}

