.orange-pattern-bg {
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    background: url('/src/assets/images/orange-pattern-bg.webp');
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
  }
}

.signup {
  margin-top: -191px;

  @media (max-width: 900px) {
    margin-top: 0;
  }

  .content-wrapper {
    padding: 80px 0;
    background: #000000eb;
    text-align: center;
  }
}