.primary-nav {
  ul {
    list-style: none;
    padding: 0;
    display: flex;

    @media (max-width: 600px) {
      margin: 0;
    }

  }

  li {
    padding: 20px;

    @media (max-width: 900px) {
      padding: 10px;
    }

    @media (max-width: 600px) {
      padding-top: 7px;
    }

    a {
      text-decoration: none;
      font-weight: 400;
      color: inherit;
      font-size: 20px;

      @media (max-width: 900px) {
        font-size: 14px;
      }
    }
  }
}