.footer {
  &__content {
    list-style: none;
    color: #fff;
    padding: 25px 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;

    @media (max-width: 900px) {
      flex-direction: column;
    }

  }

  &__item {
    font-size: 12px;
    font-weight: 600;
    line-height: 20px;

    @media (max-width: 1200px) {
      font-size: 13px;
      font-weight: 400;
    }

        @media (max-width: 900px) {
          margin-bottom: 30px;
        }
  }

  &__link {
    text-decoration: none;
    color: inherit;
    font-size: 20px;
    line-height: 24px;
    font-weight: 400;

    @media (max-width: 1200px) {
      font-size: 13px;
    }
  }
}