.feature-infographic {
  width: 100%;
  border-spacing: 0;

  &__list {
    @media (max-width: 900px) {
      position: static !important;
    }
  }

  &__list-index {
    width: 222px;
    height: 118px;
    padding: 32px;
    font-size: 48px;
    font-weight: 700;

    span {
      @media (max-width: 1024px) {
        display: none;
      }
    }

    @media (max-width: 1024px) {
      width: auto;
    }

    @media (max-width: 900px) {
      display: none;
    }
  }

  &__list-separator {
    width: 80px;

    img {
      display: block;
    }

    @media (max-width: 900px) {
      display: none;
    }
  }

  &__list-description {
    padding-left: 80px;
    display: flex;
    align-items: center;
    height: 87px;

    @media (max-width: 1200px) {
      padding-left: 40px;
      padding-right: 20px;
    }

    @media (max-width: 1024px) {
      padding-left: 20px;
    }

    @media (max-width: 900px) {
      min-height: 150px;
      ;
    }

    @media (max-width: 480px) {
      min-height: 280px;
    }


    img {
      width: 50px;
      margin-right: 45px;
    }

    p {
      margin: 0;
      font-size: 20px;
      line-height: 24px;
      max-width: 720px;

      @media (max-width: 900px) {
        font-size: 16px;
      }
    }
  }

  &__list:last-child {
    overflow: hidden;

    td:last-child::after {
      content: '';
      height: 350px;
      width: 350px;
      border-radius: 10000px;
      background-color: #0295ff47;
      position: absolute;
      right: 20%;
      filter: blur(150px);
    }
  }
}