.wrapper {
  padding-left: 15px;
  padding-right: 15px;
  max-width: 1470px;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
  position: relative;

  &--hero-bg {
    overflow: initial;

    &::after {
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      right: -200px;
      z-index: -5;
      background: url('/src/assets/images/redish-backgroundwebp.webp') no-repeat;
    }
  }

  &--medium {
    max-width: 976px;
  }

  &--no-padding-untill-medium {
    padding-left: 0;
    padding-right: 0;

    @mixin atMedium {
      padding-left: 18px;
      padding-right: 18px;
    }
  }

  &--no-padding-untill-large {
    padding-left: 0;
    padding-right: 0;

    @mixin atLarge {
      padding-left: 18px;
      padding-right: 18px;
    }
  }

  &--b-margin {
    margin-bottom: 1rem;

    @mixin atSmall {
      margin-bottom: 3.875rem;
    }
  }

  .wrapper {
    padding-left: 0px;
    padding-right: 0px;
  }
}