.large-hero {
  position: relative;
  min-height: 800px;

  @media (max-width: 1200px) {
    min-height: 500px;
  }

  &::before {
    content: '';
    position: absolute;
    bottom: 0;
    height: 500px;
    width: 500px;
    background: radial-gradient(circle, rgba(2, 149, 255, 0.03) 0%, rgba(2, 149, 255, 0.4) 36%, rgba(0, 149, 255, 0.8) 100%);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    filter: blur(90px);
    border-radius: 50%;
    opacity: 0.3;
    left: 100px;
  }

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;

    &--horizontal {
      z-index: -1;
    }

    &--formula {
      z-index: -2;
    }
  }

  &__text-content {
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -30%);
    text-align: center;

    @media (max-width: 600px) {
      min-width: 580px;
    }

    @media (max-width: 480px) {
      max-width: 300px;
      min-width: 300px;
    }

  }

  &__title {
    font-size: 24px;
    font-weight: 400;
    margin-bottom: 90px;

    @media (max-width: 1200px) {
      font-size: 20px;
      margin-bottom: 45px;
    }
  }

  &__description {
    font-size: 42px;
    line-height: 50px;
    max-width: 520px;
    margin: auto;
    margin-bottom: 85px;

    @media (max-width: 1200px) {
      font-size: 24px;
      line-height: 32px;
      margin-bottom: 55px;
    }

    @media (max-width: 600px) {
      max-width: 500px;
    }

    @media (max-width: 480px) {
      max-width: 300px;
    }

    &--xl {
      font-size: 62px;
      line-height: 73px;
      font-weight: 800;
      min-width: 730px;

      @media (max-width: 1200px) {
        font-size: 33px;
        line-height: 40px;
        margin-bottom: 35px;
      }

      @media (max-width: 600px) {
        min-width: initial
      }
    }
  }

  &__graphic-content {
    position: absolute;
    right: 0;
    bottom: 40px;

    @media (max-width: 1024px) {
      display: none;
    }
  }

  &__graphic {
    text-align: center;
    margin: 0;
    width: 273px;
    height: 166px;
    overflow: hidden;
    margin-bottom: 25px;
    border: solid 8px var(--secondary-color);


    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}